.timeline-container {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 40px 0;
    /* animation: transitionIn 0.75s; */
}

.timeline-container::after {
    background-color: #e17b77;
    content: '';
    position: absolute;
    left: calc(50% - 2px);
    width: 4px;
    height: 100%;
}
