.timeline-item {
    display: flex;
    justify-content: flex-end;
    padding-right: 30px;
    position: relative;
    margin: 10px 0;
    width: 50%;
    animation: transitionIn 0.75s;
}

.timeline-item:nth-child(even) {
    align-self: flex-end;
    justify-content: flex-start;
    padding-left: 30px;
    padding-right: 0;
}


@keyframes transitionIn {
    from {
        opacity: 0;
        transform: translateX(-50);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}
